.radio-wrap {
  flex-direction: column;
  margin-top: 10px;
  display: flex;
}

[type="radio"]:checked, [type="radio"]:not(:checked) {
  position: absolute;
  left: -9999px;
}

[type="radio"]:checked + label, [type="radio"]:not(:checked) + label {
  cursor: pointer;
  color: #907e82;
  padding-left: 28px;
  line-height: 20px;
  display: inline-block;
  position: relative;
}

[type="radio"]:checked + label:before, [type="radio"]:not(:checked) + label:before {
  content: "";
  width: 18px;
  height: 18px;
  background: #fff;
  border: 1px solid #c2e297;
  border-radius: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

[type="radio"]:checked + label:after, [type="radio"]:not(:checked) + label:after {
  content: "";
  width: 12px;
  height: 12px;
  background: #e17992;
  border-radius: 100%;
  transition: all .2s;
  position: absolute;
  top: 3px;
  left: 3px;
}

[type="radio"]:not(:checked) + label:after {
  opacity: 0;
  transform: scale(0);
}

[type="radio"]:checked + label:after {
  opacity: 1;
  transform: scale(1);
}

[type="radio"]:checked + .label-wrap__name {
  color: currentColor;
}

.label-radio + .label-radio {
  margin-top: 10px;
}

